
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import value2name from 'vux/src/filters/value2name.js'

import userphoto from "../../../static/boy.png";
export default {
name: 'page',
components: {
XHeader,
Group,
XInput,
XAddress,
XButton,
Cell,
Datetime,
PopupPicker,
XTextarea,
XNumber,
Checker,
CheckerItem
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "",
more_text: "",
isShowMore: false,
birthday: '2015-11-12',
address: "",
username: "",
userphone: "",
selectAddress: "",
value_0_1: [],
value: [],
title2: '',
value2: ['', '', ''],
value3: ['', '', '--'],
addressData: ChinaAddressV4Data,
value4: [],
value5: ['', ' ', ''],
showAddress: false,
title1: '',
list1: [
['', '', '', '', '', '']
],
value1: [''],
textdesc: "",
detailedaddress: "",
adorn_money: 0,
adorn_area: 0,
adorn_style: "",
adorn_type: 1,
uid: 0,
staffuid: 0,
userinfo: {},
headurl: userphoto
}
},
created() {
this.uid = this.$route.params.uid;
this.staffuid = this.util.getUid();
if(this.uid!=0){
this.onLoadPro();
}
console.log(this.uid)
},
methods: { //
onLoadPro: function() {
let params = {
uid: this.uid
}

this.$axios.post(this.umcons.serviceAddress.USER_INFO, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.userinfo = data;
this.username = data.name;
this.userphone = data.phone;
this.detailedaddress = data.address;
this.headurl = data.headurl;
this.adorn_area = parseInt(data.area);
this.adorn_money = parseInt(data.price);
if(data.style != undefined) {
this.adorn_style = data.style;
}
this.adorn_type = data.type;

if(data.birthday != undefined) {
this.birthday = data.birthday;
}
if(data.remark != undefined) {
this.textdesc = data.remark;
}
if(data.source != undefined) {
this.value1 =new Array(data.source) ;
}
console.log(response);
} else {
this.util.showAlert("");
}

}.bind(this));
},
onAddClint: function() {

let params = {
uid: this.staffuid,
user_uid: this.uid,
name: this.username,
phone:this.userphone,
address:this.selectAddress+this.detailedaddress,
area: this.adorn_area,
style: this.adorn_style,
type: this.adorn_type,
price: this.adorn_money,
birthday: this.birthday,
remark: this.textdesc,
source: this.value1[0]
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.EDIT_CUSTOMER, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.util.showAlert("");
var data = response.data.data;
}

}.bind(this));
},
doShowAddress() {
this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
onShadowChange(ids, names) {

console.log(names);
this.selectAddress = names[0] + names[1] + names[2];
},
changeData() {
this.value2 = ['430000', '430400', '430407']
},
changeDataByLabels() {
this.value2 = ['', '', '']
},
changeDataByLabels2() {
this.value2 = ['', '', '--']
},
getName(value) {
return value2name(value, ChinaAddressV4Data)
},
logHide(str) {
console.log('on-hide', str)
},
logShow(str) {
console.log('on-show')
},
log(str1, str2 = '') {
console.log(str1, str2)
},
onConfirm(val) {
console.log('on-confirm arg', val)
console.log('current value', this.value1)
},
change(value) {
console.log('change', value)
},
onShow() {
console.log('on show')
},
onHide(type) {
console.log('on hide', type)
},
onChange(val) {
console.log('val change', val)
},
onEvent(event) {
console.log('on', event);
},

showMore: function() {
if(this.isShowMore == true) {
this.more_text = "";
this.isShowMore = false;
} else {

this.more_text = "";
this.isShowMore = true;
}

}

}
}



