
import Datetime from 'vux/src/components/datetime/index.vue'

export default {
name: "stepsbar",
components: {
Datetime
},
data() {
return {

}
},
props: ["flowslist"],
created() {

},
methods: { //
formateState: function(state) {
if(state == 0) {
return ""
} else if(state == 1) {
return ""
} else {
return ""
}

},
onselect: function(index) {
var state = this.flowslist[index].state;
if(state <= 1) {
this.$emit('onschedule', index);
} else {
this.util.showAlert("");
}

},ontimestart:function(index){
this.$emit('timestart', index);
},ontimeend:function(index){
this.$emit('timeend', index);
}
}
}



